    display: flex;
    justify-content: center;
    align-items: center;
    /* width:60px; */
   
    justify-self: center;

}
#fileinput input{
    display: flex;
    justify-content: center;
    align-items: center;
    /* width:60px; */
     width:178px;
    justify-self: center;
    background-color:  hsl(289, 37%, 23%) ;
    color: rgb(252, 169, 245);
    border: 2px solid rgb(87 14 81); 
    border-radius: 6px;
    margin: 2px 2px;

}

.PostComp textarea{
    color: rgb(87 14 81);
    background-color:#e2b5ed;
    border: 3px solid rgb(87 14 81);
    border-radius: 5px;
    
}

